* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Employer Dashboard */

.employer-dashboard {
  display: flex;
  height: 70vh;
}

.employer-dashboard > div:nth-of-type(1) {
  width: 250px;
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  align-items: flex-start;
  padding: 20px;
}

.employer-dashboard > div:nth-of-type(1) > * {
  margin: 20px;
}

.employer-dashboard > div:nth-of-type(1) > button {
  padding: 5px 20px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 15px;
  width: 100px;
}

.employer-dashboard > div:nth-of-type(1) > button:hover {
  cursor: pointer;
  background-color: black;
  color: white;
}

.employer-dashboard-extention {
  width: 100%;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Employer Profile */

.employer-profile {
  background-color: #f7f7f7;
  border-radius: 10px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  padding: 20px;
  text-align: center;
  width: 500px;
  height: 500px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.employer-profile p {
  font-size: 18px;
  margin: 5px 0;
}

.employer-profile button {
  background-color: #04AA6D;
  border: none;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  font-size: 16px;
  margin-top: 10px;
  padding: 10px 20px;
  transition: background-color 0.3s ease;
}

.employer-profile button:hover {
  background-color: #3e8e41;
}

/* UPDATE FORM */
.update-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 50%;
  margin: 0 auto;
  text-align: center;
}

.update-form input,
.update-form select {
  width: 100%;
  padding: 0.5rem;
  margin: 0.5rem 0;
  border: none;
  border-radius: 5px;
  background-color: #f7f7f7;
  font-size: 1rem;
  color: #333;
}

.update-form input[type='submit'] {
  width: auto;
  background-color: #2b2d42;
  color: #fff;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.update-form input[type='submit']:hover {
  background-color: #8d99ae;
}

.update-form button {
  background-color: #fff;
  color: #333;
  padding: 0.5rem 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.update-form button:hover {
  background-color: #f7f7f7;
}

.update-form div {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.update-form div input {
  flex-basis: calc(50% - 0.5rem);
}

.update-form div select {
  flex-basis: 100%;
  margin-top: 0.5rem;
}

/* Search services */

.search-services-container {
  display: flex;
  flex-direction: column;
}

.search-services {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 20px;
}

.search-services select {
  margin-left: 10px;
  padding: 5px;
}

.search-services button {
  margin-left: 10px;
  border: none;
  background: none;
  font-size: 20px;
  cursor: pointer;
}

.employee-container {
  display: flex;
  flex-direction: column;
}

.employee-container h4 {
  margin: 0;
  margin-bottom: 5px;
}

.employee-container p {
  margin: 0;
  margin-bottom: 10px;
}

.employee-container button {
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  background-color: #4CAF50;
  color: white;
  font-size: 16px;
  cursor: pointer;
}

/* Serch-service results */

.search-services-results {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  margin-top: 20px;
}

.search-services-results div {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f5f5f5;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.search-services-results h4 {
  margin-top: 0;
}

.search-services-results p {
  margin-bottom: 10px;
}

.search-services-results button {
  background-color: #4caf50;
  color: #fff;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.search-services-results button:hover {
  background-color: #3e8e41;
}

/* Expanded Employee */

.expanded-employee {
  background-color: #f2f2f2;
  border: 1px solid #ddd;
  padding: 10px;
  margin-top: 20px;
  border-radius: 5px;
}

.expanded-employee h4 {
  font-size: 1.5rem;
  margin: 0 0 5px;
}

.expanded-employee p {
  font-size: 1rem;
  margin: 0;
  padding: 5px 0;
}

.expanded-employee p:last-child {
  border-bottom: none;
}

.expanded-employee button {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1rem;
  margin-top: 10px;
  cursor: pointer;
  border-radius: 5px;
}


