* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

.register-signup {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px;
}

.employer-feedback {
    display: none;
}

.register-signup-selection {
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 50vh;
    padding: 10px;
    margin: 0 auto;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.register-signup-selection > div:nth-of-type(1) {
    margin-bottom: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.register-signup-selection > div:nth-of-type(1) > p:nth-of-type(1) {
    font-size: 40px;
    letter-spacing: 1px;
}

.register-signup-selection > div:nth-of-type(2) {
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.register-signup-selection div button{
    padding: 10px 20px;
    width: 200px;
    background-color: #4668b3;
    color: #f7f7f7;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 10px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
}

.register-signup-selection div button:hover {
    background-color: #2d4373;
}

.register-signup-selection > p {
    text-align: center;
}

.login-form {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login-form p {
    font-size: 1rem;
    margin-bottom: 1rem;
}
.login-form input {
    width: 100%;
    padding: 0.8rem;
    margin-bottom: 0.5rem;
    border-radius: 3px;
    border: none;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2);
}
.login-form input[type="submit"] {
    box-shadow:none;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.login-form input[type="submit"]:hover {
    background-color: #3e8e41;
    color: white;
}

.register-form {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
  
.register-form p {
    font-size: 1rem;
    margin-bottom: 1rem;
}

.register-form input {
    width: 100%;
    padding: 0.8rem;
    margin-bottom: 0.5rem;
    border-radius: 3px;
    border: none;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2);
}

.register-form input[type="submit"] {
    box-shadow:none;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
  
.register-form input[type="submit"]:hover {
    background-color: #3e8e41;
    color: white;
}
  

.additional-form-togglers {
    border: none;
    background-color: transparent;
}

.forgot-pass {
    background-color: transparent;
    border: none;
    color: #3b5998;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    margin-top: 20px;
    transition: color 0.2s ease-in-out;
}
  
.forgot-pass:hover {
    color: #2d4373;
}

@media only screen and (min-width: 700px) {
    .register-signup {
        flex-direction: row;
        align-items: center;
        width: 70%;
        margin: 10px auto;
        min-height: 90vh;
        background-color: #fff;
    }

    .employer-feedback {
        display: block;
        max-width: 500px;
        margin-right: 50px;
        text-align: justify;
    }

    .employer-feedback p {
        font-size: 24px;
        font-weight: bold;
        margin-bottom: 20px;
    }
      
    .employer-feedback div {
        font-size: 16px;
        line-height: 1.5;
        color: #555;
    }

    .register-signup-selection {
        /* width: 400px; */
        /* margin: 0; */
        display: flex;
        flex-direction: column;
        max-width: 400px;
    }

    .register-signup-selection div {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 20px;
        /* background-color: #fff; */
        border-radius: 10px;
        /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); */
      }
    .signup > *{
        background-color: rgb(148, 132, 132);
    }
}